import React, { useEffect, useState } from 'react';
import cls from './MainComponent.module.css';
import YandexMapComponent from "../YandexMapComponent/YandexMapComponent";
import { Box, Typography, Button, Modal } from "@mui/material";
import LandscapeIcon from '@mui/icons-material/Landscape';
import AddMarkForm from "../YandexMapComponent/AddMarkForm";
import {
    getLocationCount,
    getUserCount,
    deleteLocation
} from "../../services/mainService";

const MainComponent = () => {
    const [isFormOpen, setFormOpen] = useState(false);
    const [editingMark, setEditingMark] = useState(null);
    const [locations, setLocations] = useState([]);
    const [address, setAddress] = useState('');
    const [mark, setMark] = useState(null);
    const [currentUser, setCurrentUser] = useState({ id: 1, name: 'Иван Иванов' });
    const [userCount, setUserCount] = useState(0);

    useEffect(() => {
        refreshData();
    }, []);

    const refreshData = () => {
        getLocationCount()
            .then(data => setLocations(data))
            .catch(err => console.error(err));
        getUserCount()
            .then(res => setUserCount(res.user_count))
            .catch(err => console.error(err));
    };


    const handleEditClick = () => {
        if (!editingMark) return;
        setFormOpen(true);
    };

    const handleDeleteClick = async () => {
        if (!editingMark) return;
        try {
            await deleteLocation(editingMark.id);
            setEditingMark(null);
            refreshData();
        } catch (err) {
            console.error(err);
        }
    };

    return (
        <div className={cls.mainBlock}>
            <Typography
                variant="h1"
                sx={{
                    fontSize: '35px',
                    fontWeight: '500',
                    display: 'flex',
                    alignItems: 'center',
                    gap: '10px'
                }}
            >
                EcoMap <LandscapeIcon fontSize="large" />
            </Typography>

            <Box sx={{
                width: { xs: '100%', md: '50%' },
                height: { xs: '100%', md: '50%' },
                borderRadius: "10px",
                overflow: "hidden"
            }}>
                <YandexMapComponent
                    address={address}
                    mark={mark}
                    setMark={setMark}
                    setAddress={setAddress}
                    locations={locations}
                    selectedMark={editingMark}
                    onSelectedMark={setEditingMark}
                    currentUser={currentUser}
                />
            </Box>

            <Box sx={{ display: 'flex', gap: 2, mt: 2 }}>
                <Button
                    variant="contained"
                    onClick={() => {
                        setEditingMark(null);
                        setFormOpen(true);
                    }}
                >
                    Добавить отметку
                </Button>

                <Button
                    variant="outlined"
                    onClick={handleEditClick}
                >
                    Редактировать
                </Button>

                <Button
                    color="error"
                    variant="outlined"
                    onClick={handleDeleteClick}
                >
                    Удалить
                </Button>
            </Box>

            <Modal open={isFormOpen} onClose={() => setFormOpen(false)}>
                <Box className={cls.modalForm}>
                    <AddMarkForm
                        mark={editingMark || mark}
                        address={address}
                        currentUser={currentUser}
                        onSaved={() => {
                            setFormOpen(false);
                            refreshData();
                        }}
                        onCancel={() => setFormOpen(false)}
                    />
                </Box>
            </Modal>
        </div>
    );
};

export default MainComponent;