import React, { useState, useEffect } from 'react';
import {
    Box, Paper, Stack, Typography,
    TextField, Button, FormControl,
    InputLabel, Select, MenuItem
} from "@mui/material";
import { addLocation, updateLocation } from "../../services/mainService";

const AddMarkForm = ({ mark, address, currentUser, onSaved, onCancel }) => {
    const isEdit = Boolean(mark?.id);

    const [form, setForm] = useState({
        name: isEdit ? mark.name : 'regular',
        address: isEdit ? mark.address : address || '',
        latitude: isEdit ? mark.latitude : (Array.isArray(mark) ? mark[0] : '') || '',
        longitude: isEdit ? mark.longitude : (Array.isArray(mark) ? mark[1] : '') || '',
    });

    useEffect(() => {
        if (isEdit) {
            setForm({
                name:      mark.name,
                address:   mark.address,
                latitude:  mark.latitude,
                longitude: mark.longitude,
            });
        } else if (Array.isArray(mark) && address) {
            setForm(prev => ({
                ...prev,
                address,
                latitude:  mark[0],
                longitude: mark[1],
            }));
        }
    }, [isEdit, mark, address]);

    const handleChange = e => {
        const { name, value } = e.target;
        setForm(prev => ({ ...prev, [name]: value }));
    };

    const handleSubmit = async e => {
        e.preventDefault();
        try {
            if (isEdit) {
                await updateLocation(mark.id, {
                    ...form,
                    addedBy: currentUser.id
                });
            } else {
                await addLocation({
                    ...form,
                    addedBy: currentUser.id
                });
            }
            onSaved();
        } catch (err) {
            console.error(err);
        }
    };

    return (
        <Paper
            component="form"
            onSubmit={handleSubmit}
            sx={{ p: 3, width: { xs: '90vw', sm: 400 }, mx: 'auto', borderRadius: 2 }}
            elevation={4}
        >
            <Stack spacing={2}>
                <Typography variant="h6" textAlign="center">
                    {isEdit ? 'Редактировать метку' : 'Новая метка'}
                </Typography>

                <FormControl fullWidth size="small">
                    <InputLabel id="select-name-label">Название</InputLabel>
                    <Select
                        labelId="select-name-label"
                        name="name"
                        value={form.name}
                        label="Название"
                        onChange={handleChange}
                    >
                        <MenuItem value="regular">Обычная</MenuItem>
                        <MenuItem value="construction">Для стройматериалов</MenuItem>
                        <MenuItem value="recycle">Пункт приёма вторсырья</MenuItem>
                    </Select>
                </FormControl>

                <TextField
                    label="Адрес"
                    name="address"
                    value={form.address}
                    onChange={handleChange}
                    required fullWidth size="small"
                />

                <Stack direction="row" spacing={1}>
                    <TextField
                        label="Широта"
                        name="latitude"
                        value={form.latitude}
                        onChange={handleChange}
                        required fullWidth size="small"
                        type="number" inputProps={{ step: "any" }}
                    />
                    <TextField
                        label="Долгота"
                        name="longitude"
                        value={form.longitude}
                        onChange={handleChange}
                        required fullWidth size="small"
                        type="number" inputProps={{ step: "any" }}
                    />
                </Stack>

                <Box sx={{ display: 'flex', justifyContent: 'flex-end', gap: 1 }}>
                    <Button onClick={onCancel} variant="outlined" size="small">
                        Отмена
                    </Button>
                    <Button type="submit" variant="contained" size="small">
                        {isEdit ? 'Сохранить' : 'Создать'}
                    </Button>
                </Box>
            </Stack>
        </Paper>
    );
};

export default AddMarkForm;