import React, { useState, useCallback } from 'react';
import {
    YMaps,
    Map,
    Placemark,
    withYMaps
} from 'react-yandex-maps';

const ICON_PRESETS = {
    regular: 'islands#blueIcon',
    construction: 'islands#orangeIcon',
    recycle: 'islands#greenIcon',
};

const InnerMap = ({ ymaps, mark, setMark, locations, address, setAddress, selectedMark, onSelectedMark }) => {
    const handleMapClick = useCallback((e) => {
        const coords = e.get('coords');

        if (ymaps?.geocode) {
            ymaps.geocode(coords).then(res => {
                const first = res.geoObjects.get(0);
                const addr = first?.getAddressLine() || '';
                setMark(coords);
                setAddress(addr);
            });
        } else {
            setMark(coords);
            setAddress('');
        }
    }, [ymaps, setMark, setAddress]);

    const renderName = (name) => {
        switch (name) {
            case 'construction':
                return 'Для стройматериалов';
            case 'recycle':
                return 'Пункт приема вторсырья';
            default:
                return 'Мусорка обычная';
        }
    };

    return (
        <Map
            defaultState={{ center: [55.75, 37.57], zoom: 10 }}
            width="100%"
            height="490px"
            onClick={handleMapClick}
        >
            {locations.map(loc => {
                const isSelected = selectedMark?.id === loc.id;
                const preset = isSelected
                    ? 'islands#redIcon'
                    : (ICON_PRESETS[loc.name] || ICON_PRESETS.regular);

                return (
                    <Placemark
                        key={loc.id}
                        geometry={[loc.latitude, loc.longitude]}
                        options={{ preset }}
                        onClick={() => onSelectedMark(loc)}
                        properties={{
                            balloonContent: `<strong>${renderName(loc.name)}</strong><br/>${loc.address}`
                        }}
                        modules={['geoObject.addon.balloon','geoObject.addon.hint']}
                    />
                );
            })}

            {mark && (
                <Placemark
                    geometry={mark}
                    options={{ preset: 'islands#redIcon' }}
                    properties={{
                        balloonContent: `<strong>Выбранная метка</strong><br/>${address}`
                    }}
                    modules={['geoObject.addon.balloon']}
                />
            )}
        </Map>
    );
};

const ConnectedMap = withYMaps(InnerMap, true, ['geocode']);

const YandexMapComponent = ({ mark, setMark, locations, address, setAddress, selectedMark, onSelectedMark }) => (
    <YMaps query={{ apikey: '5c0dcaa4-8881-4c61-af56-b55bec0a7cdd' }}>
        <ConnectedMap
            mark={mark}
            selectedMark={selectedMark}
            onSelectedMark={onSelectedMark}
            setMark={setMark}
            locations={locations}
            address={address}
            setAddress={setAddress}
        />
    </YMaps>
);

export default YandexMapComponent;