import axios from 'axios'

// ——— Общий клиент для API ———
const apiClient = axios.create({
    baseURL: 'http://127.0.0.1:8000/api/',
    withCredentials: true,       // шлёт sessionid+csrftoken куки
    headers: {
        'Content-Type': 'application/json',
    },
})

// ——— Хелпер для CSRF ———
function getCSRFToken() {
    const match = document.cookie.match(/csrftoken=([^;]+)/)
    return match ? match[1] : ''
}

// ——— Статистические GET-запросы ———
async function fetchStats(queryType) {
    try {
        const { data } = await apiClient.get('stats/', {
            params: { queryType },
        })
        return data
    } catch (err) {
        console.error(`[stats:${queryType}]`, err.response?.data || err.message)
        throw err
    }
}

export const getLocationRating      = () => fetchStats('location_rating')
export const getLocationCount        = () => fetchStats('all_locations')
export const getUserCount           = () => fetchStats('user_count')
export const getReviewCount         = () => fetchStats('review_count')
export const getCommentsByLocation  = () => fetchStats('comments_by_location')
export const getTagUsage            = () => fetchStats('tag_usage')

// ——— Универсальный POST-хелпер для action ———
async function postAction(payload) {
    try {
        const { data } = await apiClient.post(
            'action/',
            payload,
            {
                headers: {
                    'X-CSRFToken': getCSRFToken(),
                },
            }
        )
        return data
    } catch (err) {
        console.error(`[action:${payload.action}]`, err.response?.data || err.message)
        throw err
    }
}

// ——— Создание сущностей ———
export const addUser      = (user)      => postAction({ action: 'add_user',      ...user })
export const addLocation  = (location)  => postAction({ action: 'add_location',  ...location })
export const addWasteType = (wasteType) => postAction({ action: 'add_waste_type', ...wasteType })
export const addReview    = (review)    => postAction({ action: 'add_review',     ...review })

// ——— Обновление и удаление локаций ———
export const updateLocation = (id, updates) =>
    postAction({ action: 'update_location', id, ...updates })

export const deleteLocation = (id) =>
    postAction({ action: 'delete_location', id })